import { combineClasses } from '../../utils';

import cs from './styles.module.css';

import ok from './ok.svg';
import { useEffect, useRef } from 'react';
import { useAllComponents } from '../../services/burger';
import { useSendOrderMutation } from '../../services/api';

const OrderDetails = () => {
  const components = useAllComponents();
  const componentsRef = useRef(components);

  const [sendOrder, { error, data }] = useSendOrderMutation();
  useEffect(() => {
    if (componentsRef.current !== null) {
      sendOrder(componentsRef.current);
      componentsRef.current = null;
    }
  }, [sendOrder]);

  if (!data) {
    const text = error ? `: ${error.error.toString()}` : '...';
    return <div className={cs.details}>
      <p className={combineClasses('text text_type_main-medium mt-8', cs.center, error && cs.error)}>{text}</p>
    </div>;
  }

  return (
    <div className={cs.details}>
      <p className={combineClasses('text text_type_digits-large', cs.center)}>{data.order.number}</p>
      <p className={combineClasses('text text_type_main-medium mt-8', cs.center)}> </p>
      <img className={cs.image} src={ok} alt=''/>
      <p className={combineClasses('text text_type_main-default', cs.center)}>   </p>
      <p className={combineClasses('text text_type_main-default text_color_inactive mt-2', cs.center)}>    </p>
    </div>
  );
};

export default OrderDetails;
